/* eslint-disable */
import strategy from 'strategy/strategy'
export default {
	/**
	 * Get all P&l data informations from database
	 */
 async getPl() {
	return fetch('https://api.mlab.com/api/1/databases/financialindicators/collections/reporting/5b1920cf5d0e653ffab57b40?apiKey=mqZL_DwTTvjcbABJ-OmwUx2wfRKxoUbc ')
	.then((response) => response.json())
	.then((responseJson) => {
		console.log(responseJson)
		return strategy.transcodePnl(responseJson['P&L']);
	})
	.catch((error) => {
		console.error(error);
	});
 }
}



