<template>
<div>
  <div class="row">
      <div class="col-lg-3 col-sm-6" v-for="stats in data.generalInformations">
        <stats-card>
          <div class="icon-big text-center" :class="`icon-${stats.type}`" slot="header">
            <i :class="stats.icon"></i>
          </div>
          <div class="numbers" slot="content">
            <p>{{stats.title}}</p>
            {{stats.value}}
          </div>
        </stats-card>
      </div>
    </div>
     <div class="row">
    <div class="col-md-12 col-xs-12">
        <chart-card :chart-data="activityChart.data" :chart-options="activityChart.options">
          <h4 class="title" slot="title">Estimation de production</h4>
          <span slot="subTitle">Bas sur l'estimation de charge</span>
           <div slot="legend">
            <i class="fa fa-circle text-info"></i> Charge
            <i class="fa fa-circle text-warning"></i> Production
          </div>
        </chart-card>
      </div>
     </div>

    <div class="row">
      <div class="col-md-12">
        <div class="card">
          <paper-table :title="table1.title" :sub-title="table1.subTitle" :data="data.charge.data" :columns="chargeHeaders">
          </paper-table>
        </div>
      </div>
       <div class="col-md-12">
        <div class="card">
          <paper-table :title="table2.title" :sub-title="table1.subTitle" :data="data.production.data" :columns="productionHeaders">
          </paper-table>
        </div>
      </div>
    </div>
</div>
</template>
<script>
/* eslint-disable */
import ChartCard from "components/UIComponents/Cards/ChartCard.vue";
import PaperTable from "components/UIComponents/PaperTable.vue";
import plService from "repository/pnlServices.js";
import moment from "moment-es6";
import StatsCard from "components/UIComponents/Cards/StatsCard.vue";
let tableColumns = [];
const tableData = [
  {
    Type: "String",
    name: "Dakota Rice",
    salary: "$36.738",
    country: "Niger",
    city: "Oud-Turnhout"
  }
];

export default {
  components: {
    PaperTable,
    StatsCard,
    ChartCard
  },
  data() {
    return {
      data: [],
      timeValues: [],
      chargeHeaders: [],
      productionHeaders: [],
      headersChart: [],
      activityChart: {
        data: {
          labels: [],
          series: [[], []]
        },
        options: {
          seriesBarDistance: 10,
          axisX: {
            showGrid: false
          },
          height: "245px"
        }
      },
      table1: {
        title: "Plan de charge (jours)",
        subTitle: "Priode en cours",
        columns: [...tableColumns],
        data: [...tableData]
      },
      table2: {
        title: "Plan de production (jours)",
        subTitle: "Priode en cours",
        columns: [...tableColumns],
        data: [...tableData]
      }
    };
  },
  mounted() {
    this.fetchInitialData().then(res => this.parseHeaders());
  },
  methods: {
    async fetchInitialData() {
      await plService.getPl().then(res => (this.data = res));
    },
    parseHeaders() {
      this.chargeHeaders = Object.keys(this.data.charge.data[0]);
      this.productionHeaders = Object.keys(this.data.production.data[0]);
      this.chargeHeaders.forEach((item, i) => {
        if (i > 0) {
          if (i > 0) this.activityChart.data.labels.push(item);
        }
      });
      this.getAllMonth();
       this.getChartsData(this.data.charge.data, 0);
      this.getChartsData(this.data.production.data, 1);
      console.log(this.activityChart)
    },
    getAllMonth() {
      let startDateString = moment()
        .startOf("year")
        .format("YYYY-MM-DD");
      let endDateString = moment()
        .endOf("year")
        .format("YYYY-MM-DD");
      var startDate = moment(startDateString, "YYYY-MM-DD");
      var endDate = moment(endDateString, "YYYY-MM-DD").endOf("month");

      while (startDate.isBefore(endDate)) {
        this.timeValues.push(startDate.format("MM/YY"));
        startDate = startDate.add(1, "month");
      }
    },
    getChartsData(param, position) {
      const reducer = (accumulator, currentValue) => accumulator + currentValue;
      let m0 = { date: this.timeValues[0], data: [] };
      let m1 = { date: this.timeValues[1], data: [] };
      let m2 = { date: this.timeValues[2], data: [] };
      let m3 = { date: this.timeValues[3], data: [] };
      let m4 = { date: this.timeValues[4], data: [] };
      let m5 = { date: this.timeValues[5], data: [] };
      let m6 = { date: this.timeValues[6], data: [] };
      let m7 = { date: this.timeValues[7], data: [] };
      let m8 = { date: this.timeValues[8], data: [] };
      let m9 = { date: this.timeValues[9], data: [] };
      let m10 = { date: this.timeValues[10], data: [] };
      let m11 = { date: this.timeValues[11], data: [] };
      param.forEach(item => {
        Object.keys(item).forEach(key => {
          if (key === m0.date) {
            m0.data.push(item[key]);
          }
          if (key === m1.date) {
            m1.data.push(item[key]);
          }
          if (key === m2.date) {
            m2.data.push(item[key]);
          }
          if (key === m3.date) {
            m3.data.push(item[key]);
          }
           if (key === m4.date) {
            m4.data.push(item[key]);
          }
          if (key === m5.date) {
            m5.data.push(item[key]);
          }
          if (key === m6.date) {
            m6.data.push(item[key]);
          }
          if (key === m7.date) {
            m7.data.push(item[key]);
          }
          if (key === m8.date) {
            m8.data.push(item[key]);
          }
          if (key === m9.date) {
            m9.data.push(item[key]);
          }
          if (key === m10.date) {
            m10.data.push(item[key]);
          }
          if (key === m11.date) {
            m11.data.push(item[key]);
          }
        });
      });
      let total = [m0, m1, m2, m3, m4, m5, m6, m7, m8, m9, m10, m11];
      total.forEach(m => {
        this.activityChart.data.series[position].push(m.data.reduce(reducer))
        })
    }
  }
};
</script>
<style>
.card .numbers {
  font-size: 1em !important;
  text-align: right;
}
</style>



