/* eslint-disable */
import lodash from 'lodash'
export default {

    transcodePnl(param) {
        console.log(param, 'param')
        const ressource = param[18].C
        const activity = param[116].C
        const m1 = param[18].AL
        const m2 = param[18].AM
        const m3 = param[18].AN
        const m4 = param[18].AO
        const m5 = param[18].AP
        const m6 = param[18].AQ
        const m7 = param[18].AR
        const m8 = param[18].AS
        const m9 = param[18].AT
        const m10 = param[18].AU
        const m11 = param[18].AV
        const m12 = param[18].AW
        let objToReturn = {
            production: {
                data: []
            },
            charge: {
                data: []
            },
            generalInformations: [
                {
                    type: 'success',
                    icon: 'ti-server',
                    title: 'Projet',
                    value: param[3].C
                }, {
                    type: 'warning',
                    icon: 'ti-user',
                    title: 'CP',
                    value: param[4].C,
                }, {
                    type: 'danger',
                    icon: 'ti-user',
                    title: 'DS',
                    value: param[5].C,
                }, {
                    type: 'primary',
                    icon: 'ti-user',
                    title: 'RC',
                    value: param[6].C,
                }
            ]
        }
        let parsedCharge = []
        let parsedProduction = []
        param.map((obj, i) => {
            if (i > 19 && i < 101 && obj.B !== '-') {
                parsedCharge.push({
                    ressource: obj.C,
                    [m1]: obj.L,
                    [m2]: obj.M,
                    [m3]: obj.N,
                    [m4]: obj.O,
                    [m5]: obj.P,
                    [m6]: obj.Q,
                    [m7]: obj.R,
                    [m8]: obj.S,
                    [m9]: obj.T,
                    [m10]: obj.U,
                    [m11]: obj.V,
                    [m12]: obj.W,
                })
            }
            if (i > 116 && i < 169 && obj.B) {
                parsedProduction.push({
                    activity: obj.C,
                    [m1]: obj.L,
                    [m2]: obj.M,
                    [m3]: obj.N,
                    [m4]: obj.O,
                    [m5]: obj.P,
                    [m6]: obj.Q,
                    [m7]: obj.R,
                    [m8]: obj.S,
                    [m9]: obj.T,
                    [m10]: obj.U,
                    [m11]: obj.V,
                    [m12]: obj.W,
                })
            }
        })
        console.log(parsedProduction)
        objToReturn.charge.data = parsedCharge
        objToReturn.production.data = parsedProduction
        return objToReturn
    }
}


